import React from 'react'

import './SecretInput.css'

class SecretInput extends React.Component {

  constructor(props) {
    super(props)

    this.input = React.createRef()
    this.state = {
      value: '',
      focused: false,
      isComplete: this.props.secret === ''
    }
  }
  
  componentDidMount = () => {
    this.input.current.focus()
  }

  handleClick = () => {
    this.input.current.focus();
  }

  handleFocus = () => {
    this.setState({ focused: true });
  }

  handleBlur = () => {
    this.input.current.focus();
    // this.setState({
    //   focused: false,
    // });
  }

  handleKeyUp = e => {
    if (e.key === "Backspace") {
      this.setState(state => {
        return {
          value: state.value.slice(0, state.value.length - 1),
        };
      });
    }
  }

  handleChange = e => {
    const value = e.target.value
    const secretLength = this.props.secret.length

    const newValue = (this.state.value + value).slice(0, secretLength)

    const isComplete = newValue.toLowerCase() === this.props.secret.toLowerCase()

    if (isComplete) {
      this.props.onSecretComplete()
    }

    this.setState(state => {
      if (state.value.length >= secretLength) return null;
      return {
        value: newValue,
        isComplete
      }
    })
  }

  render() {
    const { value, focused } = this.state

    const secretLength = this.props.secret.length

    const values = value.split('')
    
    const selectedIndex =
      values.length < secretLength ? values.length : secretLength - 1;

    const hideInput = !(values.length < secretLength);

    const overlays = []

    const filled = values.length === secretLength

    for (let i = 0; i < secretLength; i++) {
      const selected = values.length === i
      const filledAndLast = filled && i === secretLength - 1
      let className = 'SecretInput-display'
      if (this.state.isComplete) {
        className = `${className} SecretInput-display-complete`
      } else if (filled) {
        className = `${className} SecretInput-display-incorrect`
      } else if ((selected || filledAndLast) && focused) {
        className = `${className} SecretInput-display-selected`
      }
      

      overlays.push(
        <div className={ className } key={ `display-${i}` }>
          { values[i] }
          { (selected || filledAndLast) && focused && <div className="SecretInput-shadows" /> }
        </div>
      )
    }

    let wrapClassName = 'SecretInput-wrap'

    if (this.state.isComplete) {
      wrapClassName = `${wrapClassName} SecretInput-wrap-complete`
    } else if (filled) {
      wrapClassName = `${wrapClassName} SecretInput-wrap-incorrect`
    } 

    let className = 'SecretInput'

    if (this.props.className) {
      className = `${className} ${this.props.className}`
    }

    return (
      <div className={ className }>
        <div className={ wrapClassName } onClick={this.handleClick}>
          <input
            value=""
            ref={this.input}
            onChange={this.handleChange}
            onKeyUp={this.handleKeyUp}
            onFocus={this.handleFocus}
            onBlur={this.handleBlur}
            className="SecretInput-input"
            style={{
              width: "32px",
              top: "0px",
              bottom: "0px",
              left: `${selectedIndex * 32}px`,
              opacity: hideInput ? 0 : 1,
            }}
          />
          { overlays }
        </div>
      </div>
    )
  }
}

export default SecretInput;