function getStepRoutes() {
  const routes = []

  for (let i = 0; i < steps.length; i++) {
    const step = steps[i]
    const isLastStep = i === steps.length - 1
    const nextPath = isLastStep ? endPath: steps[i + 1].path
    const nextButtonContent = isLastStep ? 'Finish' : 'Next'
    routes.push(
      <Route
        path={ step.path }
        key={ `path ${step.path}` }
      >
        <Step
          step={ step }
          nextStepPath={ nextPath }
          nextButtonContent={ nextButtonContent }
        />
      </Route>
    )
  }
  return routes
}

function App() {
  return (
    <Router>
      <Switch>
        { getStepRoutes() }
        <Route path={ endPath }>
          <End />
        </Route>
        <Route path="">
          <Welcome
            firstStepPath={ steps[0].path }
          />
        </Route>
      </Switch>
    </Router>
  );
}

export default App;
