import React from 'react';

import './Button.css';

export default (props) => {

  let className = "Button"
  if (props.className) {
    className = `${className} ${props.className}`
  }

  if (props.disabled) {
    className = `${className} Button-disabled`
  }

  return (
    <button className={ className } onClick={ props.onClick }>
      { props.title }
    </button>
  );
}
