import React from 'react';
import { withRouter } from 'react-router-dom';

import Hint from './Hint';
import SecretInput from './SecretInput';

import Button from './Button';

import './Step.css';

class Step1 extends React.Component {
  constructor(props) {
    super(props)

    this.onSecretComplete = this._onSecretComplete.bind(this)

    this.onNextClick = this._onNextClick.bind(this)

    this.state = {
      isComplete: false
    }
  }

  _onSecretComplete() {
    this.setState({
      isComplete: true
    })
  }

  _onNextClick() {
    if (this.state.isComplete) {
      this.props.history.push(this.props.nextStepPath)
    }
  }

  render() {
    return (
      <div className="Step">
        <div className="Step-content">
          <header className="Step-title">
            { this.props.step.title }
          </header>
          <div className="Step-description">
            { this.props.step.description }
          </div>
          <SecretInput
            className="Step-secret-input"
            secret={ this.props.step.secret }
            onSecretComplete={ this.onSecretComplete }
          />
          <Hint
            hints={ this.props.step.hints }
          />
          <Button
            className="Step-start-button"
            onClick={ this.onNextClick }
            title={ this.props.nextButtonContent }
            disabled={ !this.state.isComplete }
          />
        </div>
      </div>
    );
  }
}

export default withRouter(Step1);
