import React from 'react';

import Button from './Button';

import './Hint.css';

class Hint extends React.Component {
  constructor(props) {
    super(props)

    this.onShowHintClick = this._onShowHintClick.bind(this)

    this.state = {
      hintsToShow: 0
    }
  }

  _onShowHintClick() {
    console.log('yo', this._disabled, this.state.hintsToShow)
    if (!this._disabled()) {
      this.setState({
        hintsToShow: this.state.hintsToShow + 1
      })
    }
  }

  _disabled() {
    return this.state.hintsToShow >= this.props.hints.length
  }

  render() {
    const hints = []
    for (let i = 0; i < this.state.hintsToShow; i++) {
      hints.push(
        <div
          className="Hint-hint-item"
          key={`Hint ${ i }`}
        >
          <header className="Hint-hint-header">
            Hint { i + 1 }:
          </header>
          { this.props.hints[i] }
        </div>
      )
    }

    const showHintMessage = [
      'Show a Hint',
      'Show Another Hint',
      'Another hint? Lol'
    ]

    const noMoreHintsMessage = 'Lol pathetic'

    let hintMessageToShow = ''
    let hintButtonDisabled = false

    if (this._disabled()) {
      hintMessageToShow = noMoreHintsMessage
      hintButtonDisabled = true
    } else {
      const index = Math.min(this.state.hintsToShow, showHintMessage.length - 1)
      hintMessageToShow = showHintMessage[index]
    }
      

    return (
      <div className="Hint">
        { hints }
        <Button
          className="Hint-show-button"
          disabled={ hintButtonDisabled }
          title={ hintMessageToShow }
          onClick={ this.onShowHintClick }
        />
      </div>
    );
  }
}

export default Hint;
